@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300;400;700&display=swap);
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html {
    scroll-behavior: smooth;
}
body {
    font-family: 'Kumbh Sans', sans-serif;
}
.contact {
    margin: 20vh 0;
    display : grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: center;
    height: 70vh;
}
@media screen and (max-width : 960px) {
    .contact {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
    }
    .contact form {
        width: 50%;
        margin: 42px auto;
    }
}
@media screen and (max-width : 560px) {
    .contact form {
        width: 90%;
        margin: 42px auto;
    }
}
.hero-container {
    margin-top: 10vh;
    font-family: "kumbh Sans", sans-serif;
    font-size: 32px;
    text-align: center;
}
.top-home-page {
    position: relative;
    margin-bottom: 72px;
}
.top-home-page img {
    height: 90vh;
    width: 100vw;    
}
.hero {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #FFF;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
}
.hero h1 {
    line-height: 1;
}
.hero-btns {
    width: 100%;
}
.hero .hero-btns a {
    text-decoration: none;
}
.hero .hero-btns button {
    font-family: "kumbh Sans", sans-serif;
    width: 45%;
    margin: 4px 8px;
    background: transparent;
    font-size: 18px;
    color: #FFF;
    border : 1px solid #FFF;
    border-radius: 0;
}
.hero .hero-btns button:hover {
    background-color: #FFF;
    color: #000;
    border : 1px solid #000
}
@media screen and (max-width : 560px) {
    .hero {
        width: 100%;
        font-size: 28px;
    }
    .hero-btns {
        display: flex;
        flex-direction: column;
        margin-top: 16px;
    }
    .hero .hero-btns button {
        width: 100%;
    }
    .hero-container {
        font-size: 18px;
    }
    
}
.bottom-home-page {
    height: 80vh;
    width: 90vw;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 48px;
}
.bottom-home-page img {
    width: 50%;
    height: 100%;
}
.description button {
    margin-top: 16px;
    border-radius: 0%;
    width: 50%;
    border: 1px solid #000;
}
.description {
    width: 50%;
}
.top-ad, .bottom-ad {
    display: flex;
    flex-direction: row;
    height: 50%;
    align-items: center;
}

@media screen and (max-width: 960px) {
    .hero {
        width: 70%;
    }
    
    .bottom-home-page {
        height: auto;
    }
    .top-ad, .bottom-ad {
        display: flex;
        flex-direction: column;
        height: 50%;
        align-items: center;
        padding: 24px;
    }
    .description {
        padding: 24px 0;
        width: 100%;
    }
    .bottom-home-page img {
        width: 100%;
        height: 100%;
    }
}

  .loader,
.loader:before,
.loader:after {
  background: #ffffff;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: blue;
  text-indent: -9999em;
  margin: 88px auto;
  position: absolute;
  top : 50%;
  left : 50%;
  transform: translate(-50%, -50%);
  font-size: 24px;
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
.categories {
    display: flex;
    justify-content: center;
    font-family: "Kumbh Sans", sans-serif;
    background-color: #FFF;
    height: 10vh;
    align-items: center;
    border-bottom: #345588 solid 1px;
    margin-top: 10vh;
}
.categoriesList {
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: space-around;
    width: 90%;
    border-bottom: 1px solid #FFF;
    padding: 16px;
}
.dropDown {
    display: none;
    cursor: pointer;
}
@media screen and (max-width : 960px){

    .categoriesList {
        position: absolute;
        top: -120vh;
        left: 0;
        width: 100%;
        height: 80vh;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        background-color: #FFF;
        transition: all 0.5s ease;
        z-index: -1;
    }
    .categoriesList.active {
        top: 20vh;
        transition: all 0.5s ease;
        z-index: -1;
    }
    .categoriesList li {
        border-bottom: #345588 solid 1px;
        padding: 32px;
    }
    .dropDown {
        display: block;
    }
}
 h1, h2, h3 {
    line-height: 1.6;
}
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
    background-color: #FFF;
    color: #000;
    box-shadow: 0 1px 5px #CCC;
    z-index: 5;
}
.navbar .img {
    padding-left: 10px;
}
.navbar h4 {
    display: none;
}
.navbarList {
    display: flex;
    flex-direction: row;
    font-family: 'Kumbh Sans', sans-serif;
    list-style: none;
    justify-content: space-around;
    width: 50%;
    align-items: center;
}
.navbarList li {
    padding: 8px;
    cursor: pointer;
}
.navbarList li a {
    display: block;
    text-decoration: none;
    color: #000;
}

.navbarList li a:hover {
    transform: scale(1.08);
}
.menuIcon {
    display: none;
}
.fa-home {
    font-size: 20px;
    padding: 0;
    margin-bottom: 6px;
}
@media screen and (max-width : 560px) {
    .navbar h4 {
        margin-left: 32px;
    }
    .navbar .img {
        padding-left: 4px;
    }
}
@media screen and (max-width : 960px){
    .navbar {
        margin-bottom: 8px;
    }
    .navbar h4 {
        display: block;
        margin-right: 52px;
    }
    .navbarList {
        background-color: #FFF;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 80px;
        right: -100%;
        transition: all 0.5s ease;
        z-index: 5;
      }
      .navbarList.active {
        right: 0;
        transition: all 0.5s ease;
        z-index: 5;
        background-color: rgba(256, 256, 256, 0.9);
        position: fixed;
      }
      .navbarList li {
          border-bottom: #345588 solid 1px;
          padding: 32px;
      }
      .menuIcon {
          display: block;
          font-size: 28px;
          padding-right: 16px;
          cursor: pointer;
      }
      .fa-home {
          margin-bottom: 0;
      }
}
